import React, { useState, useEffect } from "react";

const Checkbox = () => {
  const [check, setCheck] = useState(false);

  const handleOnChange = () => {
    setCheck(true);
    var cbs = document.querySelectorAll(".cb");
    cbs.forEach((cb) => {
      cb.checked = false;
    });
  }

  return (
    <div>
      <input
        type="radio"
        className={`cb`}
        onChange={handleOnChange}
        checked={check}
      ></input>
    </div>
  );
};

export default Checkbox;