import "../App.css";
import Table from "./Table.js";

const RightSection = (props) => {
  return (
    <>
      <div id="rightSection" className="doCenter">
        {props.clicked && (
          <Table
            setClicked={props.setClicked}
            srchOption={props.srchOption}
            srchInput={props.srchInput}
            setSrchInput={props.setSrchInput}
            setSrchOption={props.setSrchOption}
            selected={props.selected}
            setSelected={props.setSelected}
            setLoading={props.setLoading}
            toSend={props.toSend}
            setToSend={props.setToSend}
            referenceNo={props.referenceNo}
            setReferenceNo={props.setReferenceNo}  
          />
        )}
      </div>
    </>
  );
};

export default RightSection;
