import { useEffect } from "react";
import "../App.css";
import rakbankLogo from "../rakbank_logo.jpg";

const Header = ({ referenceNo, setReferenceNo }) => {
  useEffect(() => {
    const date = new Date();
    //const generatedUniqueNo = `${date.getFullYear()}${
    //  date.getMonth() + 1
    //}${date.getDate()}${date.getHours()}${date.getMinutes()}${date.getSeconds()}${date.getMilliseconds()}`;
    var refeNo=ajaxCall().trim()
    setReferenceNo(refeNo);
    //console.log(refeNo);
  }, []);

  function ajaxCall() {
    //console.log("---- I am called ----");

    // var params = `EventType=onLoad&LoggedInUSER=${window.parent.userName}`;
    // let url = 'http://127.0.0.1:8000/'
    // if(clicked) url += 'rakRefresh';
    // else url += 'rak'
    // var xhr;

    var params = `EventType=getHeaderDtls`;
    var url = `/TWC/CustomForms/TWC_CopyProfile/TWC_CopyProfile.jsp?${params}`;
    var xhr;

    if (window.XMLHttpRequest) xhr = new XMLHttpRequest(); // Chrome/Edge
    else if (window.ActiveXObject)
      xhr = new window.ActiveXObject("Microsoft.XMLHTTP"); // IE 5 or 6

    xhr.open("GET", url, false);
    xhr.send();

    if (xhr.status === 200) {
      var ajaxResult = xhr.responseText;
      console.log("Result", ajaxResult);
      // console.log(ajaxResult) // JSON.parse(ajaxResult)[0].title // toUse
      if (!ajaxResult)
      {
        alert("Empty response received");
        return "";
      } 
      else if (ajaxResult === "Exception")
      {
        alert("Server-side Exception occured. Please contact BPM Support.");
        return "";
      }

      let response = ajaxResult.split("|#|");
      document.getElementById('hiddenRefereNo').value=response[1];
      return response[0];
      
    } 
    else 
    {
      alert("Error while handling the request.");
      return "";
    }
  }

  return (
    <div id="header">
      <div id="left">
        <img
          id="logo"
          className="shadow"
          src={rakbankLogo}
          alt=""
          title="National Bank of Ras Al Khaimah"
        />
      </div>
      <div
        id="mid"
        className="shadow-sm rounded-1 mx-2 user-select-none"
        title="TWC- Copy Profile"
      >
        <div className="h2">
          <strong>TWC- Copy Profile</strong>
        </div>
      </div>

      <div id="right" className="doCenter">
        <div className="headerIP ">
          <label htmlFor="inputPassword" className=" headerIPLbl">
            Logged-in User:
          </label>
          <input
            className="form-control inputBox-1 shadow"
            value={window.parent.userName}
            disabled
            title={window.parent.userName}
          />
        </div>

        <div className="headerIP ">
          <input
            id="hiddenRefereNo"
            style={{display:"none"}}
            className="form-control inputBox-2 shadow"
          />
          <label htmlFor="inputPassword" className=" headerIPLbl">
            Reference Number:
          </label>
          <input
            className="form-control inputBox-2 shadow"
            value={referenceNo}
            disabled
            title={referenceNo}
          />
        </div>
      </div>
    </div>
  );
};

export default Header;
