import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import "../node_modules/bootstrap/dist/js/bootstrap.min.js";
import "./App.css";
import { useState, useMemo } from "react";
import Header from "./components/Header";
import Sidebar from "./components/Sidebar";
import PrevHistSrchTable from "./components/PrevHistSrchTable";
import RightSection from "./components/RightSection";
import Loading from "./components/Loading";

const App = () => {
  const [clicked, setClicked] = useState("");
  const [srchInput, setSrchInput] = useState("");
  const [srchOption, setSrchOption] = useState("select");
  const [selected, setSelected] = useState(null);
  const [loading, setLoading] = useState(false);
  const [toSend, setToSend] = useState([]);
  const [referenceNo, setReferenceNo] = useState("");
  
  const [refreshTable, setRefreshTable] = useState(true);

  const handleRefreshTable = () => {
    setTimeout(() => {
      setRefreshTable(false);      
    }, 1000);
    setRefreshTable(true);
  };

  
  return (
    <div>
      <Header referenceNo={referenceNo} setReferenceNo={setReferenceNo} />

      {!loading ? (
        <div id="rest" style={{ backgroundColor: "tomato" }}>
          <div id="mainContent">
            <Sidebar
              clicked={clicked}
              setClicked={setClicked}
              srchInput={srchInput}
              srchOption={srchOption}
              setSrchInput={setSrchInput}
              setSrchOption={setSrchOption}
            />
            <RightSection
              clicked={clicked}
              setClicked={setClicked}
              srchInput={srchInput}
              srchOption={srchOption}
              setSrchInput={setSrchInput}
              setSrchOption={setSrchOption}
              selected={selected}
              setSelected={setSelected}
              setLoading={setLoading}
              toSend={toSend}
              setToSend={setToSend}
              referenceNo={referenceNo}
              setReferenceNo={setReferenceNo}
            />{" "}
          </div>

          {refreshTable && <PrevHistSrchTable refreshKey={refreshTable} setRefreshKey={handleRefreshTable} />}
        </div>
      ) : (
        <Loading />
      )}
    </div>
  );
};

export default App;
