import "../App.css";

const Sidebar = ({
  clicked,
  setClicked,
  srchInput, 
  srchOption,
  setSrchInput,
  setSrchOption,

}) => {
  const handleTextOnChange = (e) => {    
    setSrchInput(e.target.value);
  };

  const handleKeyPress = (e) =>{
    if (e.keyCode === 13) { // 13 represents the Enter key
      e.preventDefault() // Prevent the default form submission behavior
      handleOnClick()
    }
  }

  const handleOptionOnChange = (e) => {
    setSrchOption(e.target.value);
    setSrchInput("");
    setClicked("");
  };

  const handleOnClick = () => {
    if (clicked === "") {
      setClicked("abc");
    } else {
      setClicked("");
      setSrchInput("");
      setSrchOption("select");
    }

    if(srchOption === 'cif_id')
    {
      if(!Number.isInteger(parseInt(srchInput)))
      {
        alert('Please provide numeric values for CIF-ID') 
        setClicked("")
        setSrchInput("")
      }
      else if(srchInput.trim().length !== 7)
      {
        alert('CIF Id should 7 digits')
        setClicked("")
        setSrchInput("")
      }
    }
  };

  return (
    <div id="sidebar" className="doCenter">
      <select
        className="dropdown shadow"
        aria-label="Default select example"
        onChange={handleOptionOnChange}
        value={srchOption}
        disabled={clicked}
      >
        <option disabled selected value="select">
          Select
        </option>
        <option value="cif_id">CIF</option>
        <option value="tl_number">TL Number</option>
        <option value="customer_name">Name of Company</option>
        <option value="rak_track_number">RAK Track Number</option>
        <option value="wi_number">WI Number</option>
        
      </select>
        <input
          className="form-control searchIP shadow"
          id="floatingInput"
          onChange={handleTextOnChange}
          value={srchInput}
          placeholder="Type here..."
          disabled={srchOption === "select" || clicked}
          maxLength={srchOption ==='cif_id' ? 7 : 100}
          minLength={srchOption ==='cif_id' ? 7 : 1}
          onKeyDown={handleKeyPress}
        />

      <div>
        <button
          id="sideBarBtn"
          onClick={handleOnClick}
          type="button"
          className={`btn btn-${clicked ? "secondary" : "danger"} m-3 shadow`}
          disabled={!srchInput.trim().length || !srchOption} 
          style={{ transition: 'background-color 0.8s ease-in-out' }}
        >
          {clicked ? "Clear" : "Search"}
        </button>
      </div>
    </div>
  );
};

export default Sidebar;
