import { useState, useEffect, useMemo } from "react";
import { useTable, usePagination, useRowSelect } from "react-table";
import "../App.css";
import { COLS } from "./Columns1";
import Checkbox from "./Checkbox";
import ProgressBar from "react-bootstrap/ProgressBar";

const Table = ({
  clicked,
  setClicked,
  srchInput,
  srchOption,
  setSrchInput,
  setSrchOption,
  selected,
  setSelected,
  setLoading,
  toSend,
  setToSend,
  referenceNo,
}) => {
  const [isLoading, setIsLoading] = useState(true);

  const columns = useMemo(() => COLS, []);

  var newans = [];

  function ajaxCall2(tempData) {
    // sending row data to JSP    
    console.log("sending -> " + tempData);
    var hiddenRefereNo = document.getElementById('hiddenRefereNo').value;
    let tempDataStr = tempData.join(","); // [a,b,c...]
    var params = `EventType=onSubbmit&ReferenceNo=${referenceNo}&HiddenReferenceNo=${hiddenRefereNo}&rowData=${tempDataStr}&sessionID=${window.parent.sessionId}`; // onSubbmit
    var url = `/TWC/CustomForms/TWC_CopyProfile/TWC_CopyProfile.jsp?${params}`;
    var xhr;
    var ajaxResult;

    if (window.XMLHttpRequest) xhr = new XMLHttpRequest(); // Chrome/Edge
    else if (window.ActiveXObject)
      xhr = new window.ActiveXObject("Microsoft.XMLHTTP"); // IE 5 or 6

    xhr.open("GET", url, false);
    xhr.send();

    if (xhr.status === 200) {
      ajaxResult = xhr.responseText;

      if (ajaxResult.trim() === "Error") {
        alert("Server-side Error occured. Please contact BPM Support.");
        return "";
      } else if (ajaxResult.trim() === "Exception") {
        alert("Server-side Exception occured. Please contact BPM Support.");
        return "";
      }

      //console.log("row data has been sent");
      return "Success";
    } else {
      alert("Error while handling the request.");
      return "";
    }
  }

  function ajaxCall() {
    var params = `EventType=onSearch&srchOption=${srchOption}&srchInput=${srchInput}&sessionID=${window.parent.sessionId}`;
    var url = `/TWC/CustomForms/TWC_CopyProfile/TWC_CopyProfile.jsp?${params}`;
    // var url = "https://api.sampleapis.com/coffee/hot";
    var xhr;
    var ajaxResult;

    if (window.XMLHttpRequest) xhr = new XMLHttpRequest(); // chrome/Edge
    else if (window.ActiveXObject)
      xhr = new window.ActiveXObject("Microsoft.XMLHTTP"); // IE 5 or 6

    xhr.open("GET", url, false);
    xhr.send();

    if (xhr.status === 200) {
      ajaxResult = xhr.responseText;
      // ajaxResult = "11117131#~# Approval#~#997621084514#~#Marcus David Butcher#~#w Approved#~#somedate#~#TWC#~#Someone#~#Omni-flow1112#~#Under Approval#~#null #~#Marcus David Butcher#~#Finance Approved#~#3rd-February 2023#~#TWC#~#Someone#~#Omni-flow|#|1113#~#Under Approval#~#null#~#abcDef#~#Finance Approved#~#3rd-February 2023#~#TWC#~#Someone#~#Omni-flow|#|1114#~#Under Approval#~# null#~#abcDef#~#Finance Approved#~#3rd-February 2023#~#TWC#~#Someone#~#Omni-flow|#|1115#~#Under Approval#~# null #~#abcDef#~#Finance Approved#~#3rd-February 2023#~#TWC#~#Someone#~#Omni-flow|#|1116#~#Under Approval#~#null#~#abcDef#~#Finance Approved#~#3rd-February 2023#~#TWC#~#Someone#~#Omni-flow|#|1117#~#Under Approval#~#12345#~#abcDef#~#Finance Approved#~#3rd-February 2023#~#TWC#~#Someone#~#Omni-flow|#|1117#~#Under Approval#~#12345#~#abcDef#~#Finance Approved#~#3rd-February 2023#~#TWC#~#Someone#~#Omni-flow|#|1117#~#Under Approval#~#12345#~#abcDef#~#Finance Approved#~#3rd-February 2023#~#TWC#~#Someone#~#Omni-flow1112#~#Under Approval#~#12345#~#abcDef#~#Finance Approved#~#3rd-February 2023#~#TWC#~#Someone#~#Omni-flow|#|1113#~#Under Approval#~#12345#~#abcDef#~#Finance Approved#~#3rd-February 2023#~#TWC#~#Someone#~#Omni-flow#1114#~#Under Approval#~#12345#~#abcDef#~#Finance Approved#~#3rd-February 2023#~#TWC#~#Someone#~#Omni-flow|#|";
      // ajaxResult = ''
      console.log("ajaxResult for Table --> ", ajaxResult);

      if (!ajaxResult) {
        setTimeout(() => {
          alert("No data found!");
          setClicked("");
          setSrchInput("");
          setSrchOption("select");
        }, 100);

      } else if (ajaxResult === "Exception") {
        alert("Server-side Exception occured. Please contact BPM Support.");
        return [];
      }

      var arr = ajaxResult.split("|#|");

      newans = []; // 2d
      for (let i = 0; i < arr.length - 1; i++) {
        newans[i] = arr[i].split("#~#");
      }

      const labels = [
        "wi_number",
        "cif_id",
        "tl_number",
        "name_of_company",
        "rak_track_number",
        "wi_created_date_time",
        "wi_status",
        "wi_completed_date_time",
        "initiation_channel",
        "wi_environment",
        "encrypted_wi_no",
      ];

      newans = newans.map((subarr) => {
        const obj = {};

        // subarr.forEach((e, i) => {
        //   const index = [0, 1, 2, 3, 4, 6, 5, 7, 8, 9][i];
        //   obj[labels[index]] = e;
        // });
        subarr.forEach((e, i) => {
          obj[labels[i]] = e.includes('null') ? '' : e;
        });
        return obj;
      });

      setTimeout(() => {
        setIsLoading(false);
      }, 1500);

      return newans;
    } else {
      alert(
        "Error while processing the request for the current Search Criteria."
      );
      setClicked("");
      setSrchInput("");
      setSrchOption("select");
      return [];
    }
  }

  var prevData;
  useState(() => {
    // New way instead of useEffect
    prevData = ajaxCall();
  }, []);

  const data = useMemo(() => prevData, []);

  const {
    getTableProps,

    getTableBodyProps,

    headerGroups,

    page,

    nextPage,

    setPageSize,

    previousPage,

    canNextPage,

    canPreviousPage,

    pageOptions,

    state,

    prepareRow,
  } = useTable(
    {
      columns,
      data,
    },
    usePagination,
    useRowSelect,
    (hooks) => {
      hooks.visibleColumns.push((columns) => {
        return [
          {
            id: "selection",
            Header: ({ getToggleAllRowsSelectedProps }) => {
              <Checkbox {...getToggleAllRowsSelectedProps()} />;
            },
            Cell: ({ row }) => (
              <Checkbox
                selected={selected}
                setSelected={setSelected}
                {...row.getToggleRowSelectedProps()}
                style={{ width: "15px" }}
              />
            ),
          },
          ...columns,
        ];
      });
    }
  );

  const { pageIndex } = state; // pageIndex destructured from state of the TableState<Object>

  useEffect(() => {
    setPageSize(5);
  }, []);

  const [progress, setProgress] = useState(0);

  useEffect(() => {
    // for Progress Bar
    const intervalId = setInterval(() => {
      setProgress((progress) => {
        if (progress >= 100) {
          clearInterval(intervalId);
          return 100;
        }
        return progress + 1;
      });
    }, 9); // Increment progress every 20 milliseconds (2 seconds / 100)

    return () => clearInterval(intervalId);
  }, []);

  function getSelectedRow() {
    return new Promise((resolve) => {
      const rows = document.querySelectorAll("#mainTable tr");

      let values = [];
      let res=[];
      Array.from(rows).forEach((row) => {
        const cells = row.querySelectorAll("td");

        const arr = Array.from(cells);
        
        arr.forEach((cell, i) => {
          if (i === 0) {
            const ip = cell.querySelector("div").querySelector("input");
            if (ip.checked) {
              values = Array.from(cells).forEach((cell,j) => {

                if(j==1 || j==10 || j==11)
                {
                  res.push(cell.innerText)
                }

              });
            }
          }
          return cell;
        });
      });

      const result = res;
      setToSend(result);
      resolve(result);
    });
  }

  const handleOnSubmitClick = () => {
    // Submit

    getSelectedRow().then((result) => {
      if (!result.length) {
        alert("Please select some data to proceed further.");
        return;
      }
      let resp = ajaxCall2(result);

      if (resp !== "Success") return;

      alert(
        "The Request has been submitted for Copy profile with Reference Number: " +
        referenceNo +
        " .You will be notified via e-mail once request will be processed."
      );
      setTimeout(() => {
        // buffer for getting into ajaxCall2
        setLoading(true);
      }, 500);

      setTimeout(() => {
        window.location.reload();
      }, 4000);
    });
  };

  return (
    <>
      {isLoading ? (
        <div style={{ width: "100%", height: "50px" }}>
          <ProgressBar animated now={progress} label="Loading.." />
        </div>
      ) : (
        <div id="tableCont" className="doCenter">
          <div id="rightSectionTable">
            <table id="mainTable" className="" {...getTableProps()}>
              <thead>
                {headerGroups.map((headerGroup) => {
                  return (
                    <tr {...headerGroup.getHeaderGroupProps()}>
                      {headerGroup.headers.map((column) => (
                        <th
                          {...column.getHeaderProps()}
                          title={column.render("Header")}
                        >
                          {column.render("Header")}
                        </th>
                      ))}
                    </tr>
                  );
                })}
              </thead>

              <tbody {...getTableBodyProps()}>
                {page.map((row) => {
                  prepareRow(row);
                  return (
                    <tr {...row.getRowProps()}>
                      {row.cells.map((cell, i) => {
                        return (
                          <td
                            {...cell.getCellProps()}
                            title={
                              cell.render("Cell").props.cell.row.cells[i].value
                            }
                          >
                            {cell.render("Cell")}{" "}
                          </td>
                        );
                      })}
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
          <div id="belowRightSectionTable">
            <div id="belowRightSectionTable-left">
              <span className="user-select-none">
                Page{" "}
                <strong>
                  {pageOptions.length ? pageIndex + 1 : 0} of{" "}
                  {pageOptions.length}
                </strong>{" "}
              </span>

              <button
                className="btn btn-secondary btn-sm"
                style={{ width: "90px", margin: "5px" }}
                onClick={() => previousPage()}
                disabled={!canPreviousPage}
              >
                {" "}
                Previous
              </button>

              <button
                className="btn btn-secondary btn-sm"
                style={{ width: "90px", margin: "5px" }}
                onClick={() => nextPage()}
                disabled={!canNextPage}
              >
                Next
              </button>
            </div>

            <div id="belowRightSectionTable-right">
              <button
                id="submit"
                type="button"
                className="btn btn-success btn-sm"
                onClick={handleOnSubmitClick}
                style={{ width: "90px", margin: "5px" }}
              >
                Submit
              </button>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default Table;
